      display: flex;
      justify-content: center;
      align-items: center; }
  .navbar__money {
    font-size: 35px;
    margin-right: 86px;
    margin-top: 25px; }
  .navbar__person {
    text-align: center;
    font-size: 18px;
    margin-right: 44px; }
    .navbar__person__name {
