.money-icon {
  width: 30px;
  height: 30px;
  margin-right: 10px; }

.person__photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-left: 23px; }

.logo {
  height: 80px;
  width: 230px;
  background: linear-gradient(45deg, #f2c744, #f2b441, #f24452);
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  font-weight: 800;
  font-size: 40px; }

.content-bar {
  margin-top: 100px;
  text-align: center;
  margin-top: 80px;
  display: flex;
  width: 100%;
  z-index: 0; }
  .content-bar .list-header {
    background: #3b3b3b;
    box-shadow: 0px 0px 8px #393939;
    font-size: 25px;
    color: white;
    font-weight: 500;
    padding: 10px;
    top: 0; }
  .content-bar .list-header-white-theme {
    color: black;
    background: #f1f1f1;
    z-index: 201; }

.work-list {
  width: 50%;
  background: #f6f6f6;
  min-height: 90vh;
  overflow: auto;
  border-right: solid 1px #7e7e7e; }
  .work-list__add-task {
    margin-top: 30px;
    margin-left: 50px;
    margin-right: 50px;
    border-radius: 20px;
    padding: 10px;
    font-size: 18px;
    cursor: pointer;
    background: #e2e2e2; }

.relax-list::-webkit-scrollbar {
  width: 10px; }

.relax-list {
  width: 50%;
  background: #f6f6f6;
  min-height: 90vh;
  overflow: auto; }
  .relax-list__header {
    box-shadow: 3px 3px 8px #b5b5b5;
    background: #fff;
    font-size: 25px;
    font-weight: 500;
    padding: 10px;
    width: 100%;
    position: fixed;
    text-align: center; }

.add-task {
  margin-top: 30px;
  margin-left: 50px;
  margin-right: 50px;
  border-radius: 20px;
  padding: 10px;
  font-size: 18px;
  cursor: pointer;
  background: #e2e2e2; }

.task-block-completed {
  display: flex;
  align-items: center; }

.task-reset-button {
  margin-left: 8px;
  padding: 5px;
  cursor: pointer;
  background-color: #ff4b4b;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px; }

.task {
  min-height: 130px;
  box-shadow: 0px 0px 8px #393939;
  background: #fff;
  margin: 60px 85px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left; }
  .task-completed {
    background: #d1e795; }
  .task__down {
    display: flex;
    justify-content: space-between;
    width: 100%; }
  .task__text {
    font-size: 19px;
    padding: 20px; }
  .task__buttons {
    display: flex;
    padding-left: 20px;
    margin-bottom: 10px;
    opacity: 0.8; }
  .task .items-right {
    display: flex; }
  .task__timer {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f24452;
    border-radius: 40px;
    padding: 15px;
    margin-bottom: -20px;
    margin-right: -12px;
    color: #fff;
    cursor: pointer;
    font-weight: 500; }
  .task__price {
    height: 53px;
    width: 53px;
    border-radius: 50%;
    background-color: #393939;
    color: #FFD949;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: -20px;
    margin-right: -10px;
    font-weight: 600;
    cursor: default; }
  .task .price-relax-theme {
    background-color: #FFD949;
    color: black; }

.add-task-modal {
  width: 400px;
  margin: 0 auto; }
  .add-task-modal__input {
    height: 60px;
    width: 400px;
    margin-top: 20px;
    border-radius: 20px;
    outline: none;
    background: #fff;
    font-family: Roboto;
    font-size: 21px;
    border: none;
    box-shadow: inset 0px 0px 3px #393939;
    padding-left: 15px; }
  .add-task-modal__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 10px; }
  .add-task-modal__price {
    display: flex;
    margin: 0px 20px;
    align-items: center; }
    .add-task-modal__price-input {
      margin-left: 10px;
      width: 60px;
      border: #5b5b5b solid 1px;
