    color: #080808;
    margin-right: 10px;
    cursor: pointer; }
  .add-task-modal__acept {
    background-image: url(/Flame1800/DayPlanApp.git/static/media/tick.3d4eb88d.svg);
    width: 26px;
    height: 26px;
    margin-right: 5px;
    cursor: pointer;
    outline: none;
    border: none;
    opacity: 1; }
  .add-task-modal__acept:hover {
    opacity: 0.8; }
  .add-task-modal__reset {
    background-image: url("https://image.flaticon.com/icons/svg/149/149690.svg");
    width: 25px;
    height: 25px;
    opacity: 0.8;
    cursor: pointer; }
  .add-task-modal__reset:hover {
    opacity: 0.5; }

.icon {
  width: 17px;
  height: 17px;
  margin: 0px 5px;
  cursor: pointer; }

.icon:hover {
  opacity: 0.6; }

.edit-button {
  background-image: url("https://image.flaticon.com/icons/svg/565/565722.svg"); }

.delete-button {
  background-image: url(/Flame1800/DayPlanApp.git/static/media/bin.74de5e29.svg); }

.ready-button {
  background-image: url("https://image.flaticon.com/icons/svg/845/845646.svg");
  width: 20px;
  height: 20px; }

.time-button {
  background-image: url(/Flame1800/DayPlanApp.git/static/media/clock.a9b56864.svg);
  width: 20px;
  height: 20px; }

.landing {
